<template>
  <div class="errorCode">
    <toast :describe="errorMsg" v-if="ToastType==='publicToast'" position="bottom"></toast>
     <toast describe="Email or Password error, please try again." v-if="ToastType==='passwordError'" position="bottom"></toast>
    <toast :describe="email + ' is not registered, please register and log in!'"  v-if="ToastType==='notRegister'" position="bottom"></toast>
    <toast :describe="email + 'is not activated yet, please activate and  log in!'" v-if="ToastType==='notActive'" position="bottom"></toast>
    <toast describe= "undefined"  v-if="ToastType==='emailLoginFailed'" position="bottom"></toast>
    <toast describe= "Email format error, please enter the correct Email !"  v-if="ToastType==='formatError'" ></toast>
    <toast :describe= "'The email:' + signEmail + ' has been registered, please login directly.'"  v-if="ToastType==='registeredError'" position="bottom"></toast>
    <toast describe= "We have sent a confirmation email, please check!"  v-if="ToastType==='success'" position="bottom"></toast>
    <toast describe="网络连接失败、服务器异常、请求超时" v-if="ToastType==='networkError'" position="bottom"></toast>
    <toast describe="undefined!" v-if="ToastType==='emailForgotFailed'" position="bottom"></toast>
    <toast :describe="forgotEmail + ' is  not registered,please sign up first !'" v-if="ToastType==='notRegistered'" position="bottom"></toast>
    <toast :describe="$t('userPage.enterPassword')" v-if="ToastType==='inputOldPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.samePasswordError')" v-if="ToastType==='sameNewPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.passwordInconsistentError')" v-if="ToastType==='newPsdDifferError'" position="bottom"></toast>
    <toast :describe="$t('userPage.newlPassword')" v-if="ToastType==='limitPsdError'" position="bottom"></toast>

    <toast :describe="$t('userPage.originalPasswordError')" v-if="ToastType==='wrongPsdError'" position="bottom"></toast>
    <toast :describe="$t('userPage.failedChangePassword')" v-if="ToastType==='changePsdFailedError'" position="bottom"></toast>
    <toast :describe="$t('userPage.successfullyChanged')" v-if="ToastType==='changePsdSuccessError'" position="bottom"></toast>
    <toast describe="Please try again for operation timeout!" v-if="ToastType==='overtime'" ></toast>

    <toast :describe="$t('userPage.onCycle')" v-if="ToastType==='setonCycleTime'" ></toast>

    <toast :describe="$t('userPage.offCycle')" v-if="ToastType==='setoffCycleTime'" ></toast>

    <toast :describe="$t('userPage.nightOnCycle')" v-if="ToastType==='setonCycleTime-night'" ></toast>

    <toast :describe="$t('userPage.nightOffCycle')" v-if="ToastType==='setoffCycleTime-night'" ></toast>

    <toast :describe="$t('userPage.dayLength')" v-if="ToastType==='setTime-moredayLength'" ></toast>

    <toast :describe="$t('userPage.nightLength')" v-if="ToastType==='setTime-morenightLength'" ></toast>

    <toast :describe="$t('userPage.dayLengthzero')" v-if="ToastType==='setTime-dayLength'" ></toast>
    <toast :describe="$t('userPage.totalTime')" v-if="ToastType==='setTime-dayLenthMore'" ></toast>
    <toast :describe="$t('userPage.dayCycleRang')" v-if="ToastType==='setTime-DaycycleTimesMore'" ></toast>
    <toast :describe="$t('userPage.nightCycleRange')" v-if="ToastType==='setTime-NightcycleTimesMore'" ></toast>
    <toast :describe="$t('userPage.dayNightRang')" v-if="ToastType==='setTime-daynightLengthMore'" ></toast>
    <toast :describe="$t('userPage.dayMnight')" v-if="ToastType==='setTime-nightMoreday'" ></toast>
    <toast :describe="$t('userPage.dayBeforenight')" v-if="ToastType==='setTime-dayLengthMorenightS'" ></toast>
    <toast :describe="$t('userPage.nightLengRange')" v-if="ToastType==='setTime-nightLengthMoreZero'" ></toast>

    <toast :describe="$t('userPage.nightLengthzero')" v-if="ToastType==='setTime-nightLength'" ></toast>

    <toast :describe="$t('userPage.dayStart')" v-if="ToastType==='setTime-dayStart'" ></toast>

    <toast :describe="$t('userPage.nightStart')" v-if="ToastType==='setTime-nightStart'" ></toast>

    <!-- <toast describe="day night 必须至少设置一种模式" v-if="ToastType==='setTime-moreThan'" ></toast> -->

    <!-- <toast describe="day Start 超过预设时间" v-if="ToastType==='setTime-front'" ></toast> -->
    <toast :describe="$t('userPage.start')" v-if="ToastType==='setTime-LessCurrTime'" ></toast>

    <!-- <toast describe="night Start 超过预设时间" v-if="ToastType==='setTime-later'" ></toast>
    <toast describe="循环间隔 超过总时间" v-if="ToastType==='setTime-later'" ></toast> -->
    <toast :describe="$t('userPage.dayorNight')" v-if="ToastType==='onemoreTimingMode'" ></toast>

    <toast :describe="$t('userPage.setTask')" v-if="ToastType==='Nosetting'" ></toast>
    <toast :describe="$t('userPage.currTask')" v-if="ToastType==='disabledrturnon'" ></toast>

    <toast :describe="$t('userPage.turnoffComm')" v-if="ToastType==='turnoffeaseyTiming'" ></toast>
    <toast :describe="$t('userPage.turnoffSenior')" v-if="ToastType==='turnoffseniorTiming'" ></toast>
    <toast :describe="$t('userPage.seniorHot')" v-if="ToastType==='turnoffseniorTimingHot'" ></toast>
    <toast :describe="$t('userPage.seniorCode')" v-if="ToastType==='turnoffseniorTimingCode'" ></toast>
    <toast :describe="$t('userPage.code')" v-if="ToastType==='turnoffCode'" ></toast>
    <toast :describe="$t('userPage.hot')" v-if="ToastType==='turnoffHot'" ></toast>
    <toast :describe="$t('userPage.addHum')" v-if="ToastType==='turnoffaddHumidity'" ></toast>
    <toast :describe="$t('userPage.reduceHum')" v-if="ToastType==='turnoffreduceHUmidity'" ></toast>

    <toast :describe="$t('userPage.alarmH')" v-if="ToastType==='Humiditythencurrent'" ></toast>
    <toast :describe="$t('userPage.alarmMH')" v-if="ToastType==='Humiditylesscurrent'" ></toast>
    <toast :describe="$t('userPage.alarmLt')" v-if="ToastType==='Temperaturethencurrent'" ></toast>
    <toast :describe="$t('userPage.alarmMt')" v-if="ToastType==='Temperaturelesscurrent'" ></toast>

    <toast :describe="$t('userPage.roomName')" v-if="ToastType==='sameRoom'" ></toast>
    <toast :describe="$t('userPage.deviceName')" v-if="ToastType==='sameDeviceName'" ></toast>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import toast from '../../components/common/toastError'
export default {
  name: 'errorCode',
  components: {
    toast
  },
  data () {
    return {}
  },
  computed: {
    ...mapState(['ToastType']),
    ...mapGetters(['errorMsg'])
  },
  watch: {
    ToastType (newVal) {
      if (newVal && newVal !== 'lowbattery') {
        console.log(newVal)
        setTimeout(() => {
          this.$store.state.ToastType = null
        }, 3000)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.errorCode{}
</style>



